'use strict';

/**
* Compute the L2-norm of a double-precision floating-point vector.
*
* @module @stdlib/blas-base-dnrm2
*
* @example
* var Float64Array = require( '@stdlib/array-float64' );
* var dnrm2 = require( '@stdlib/blas-base-dnrm2' );
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
* var N = 3;
*
* var z = dnrm2( N, x, 1 );
* // returns 3.0
*
* @example
* var Float64Array = require( '@stdlib/array-float64' );
* var floor = require( '@stdlib/math-base-special-floor' );
* var dnrm2 = require( '@stdlib/blas-base-dnrm2' );
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
* var N = floor( x.length / 2 );
*
* var z = dnrm2.ndarray( N, x, 2, 1 );
* // returns 5.0
*/

// MODULES //

var join = require( 'path' ).join;
var tryRequire = require( '@stdlib/utils-try-require' );
var isError = require( '@stdlib/assert-is-error' );
var main = require( './main.js' );


// MAIN //

var dnrm2;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dnrm2 = main;
} else {
	dnrm2 = tmp;
}


// EXPORTS //

module.exports = dnrm2;

// exports: { "ndarray": "dnrm2.ndarray" }
